import React, { Component } from "react";
import SyntaxHighlighter from 'react-syntax-highlighter';
import { hybrid } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import GitHubButton from 'react-github-btn';

import { DynamicBarChart } from "react-dynamic-charts";

import helpers from "./helpers";
import mocks from "./mocks";

import "react-dynamic-charts/dist/index.css";

export default class App extends Component {
  render() {
    return (
      <div className="main-section">
        <a href="https://github.com/dsternlicht/react-dynamic-charts">
          <img
            src="data:image/png;base64,"
            alt="Fork me on GitHub"
          />
        </a>
        <div className="chart-row">
          <div className="description">
            <div className="buttons">
              <GitHubButton href="https://github.com/dsternlicht/react-dynamic-charts" data-icon="octicon-star" data-size="large" data-show-count="true" aria-label="Star dsternlicht/react-dynamic-charts on GitHub">Star</GitHubButton>
              <GitHubButton href="https://github.com/dsternlicht/react-dynamic-charts/fork" data-icon="octicon-repo-forked" data-size="large" data-show-count="true" aria-label="Fork dsternlicht/react-dynamic-charts on GitHub">Fork</GitHubButton>
            </div>
            <h3>Hello Dynamic Charts!</h3>
            <p>React Dynamic Charts will allow you to create awesome animated dynamic charts to visualize your data.</p>
            <p>Here's the most basic example of how to use Dynamic Charts:</p>
            <SyntaxHighlighter language="javascript" style={hybrid}>
              {
`<DynamicBarChart
  data={this.state.data}
  // Timeout in ms between each iteration
  iterationTimeout={100}
/>`
              }
            </SyntaxHighlighter>
            <p>You may also add custom callbacks when animation starts, and animation ends. Check out your console to see it in action.</p>
            <SyntaxHighlighter language="javascript" style={hybrid}>
              {
`<DynamicBarChart
  data={this.state.data}
  onRunStart={() => {
    console.log("Started!");
  }}
  onRunEnd={() => {
    console.log("Ended!");
  }}
/>`
              }
            </SyntaxHighlighter>
          </div>
          <DynamicBarChart
            data={helpers.generateData(100, mocks.defaultChart, {
              prefix: "Iteration"
            })}
            iterationTimeout={100}
            showTitle={true}
            mainWrapperStyles={{
              backgroundColor: '#fff'
            }}
            chartWrapperStyles={{
              maxWidth: '1200px'
            }}
            onRunStart={() => {
              console.log("Started!");
            }}
            onRunEnd={() => {
              console.log("Ended!");
            }}
            startRunningTimeout={2500}
          />
        </div>

        <div className="chart-row">
          <div className="description">
            <h3>Custom Labels</h3>
            <p>Dynamic Charts support custom labels. So if you want to add images rather than plain text, you can!</p>
            <p>All you'll have to do is to set a React element in the `label` field as part of your data structure:</p>
            <SyntaxHighlighter language="javascript" style={hybrid}>
              {
`var data = [
  {
    id: 1,
    label: (
      <React.Fragment>
        <img src={'...'} alt="..." />
      </React.Fragment>
    )
  },
  // ...
];`
              }
            </SyntaxHighlighter>
          </div>
          <DynamicBarChart
            data={helpers.generateData(100, mocks.customLabels, { prefix: "Year", initialValue: 1980 }, 10)}
            iterationTimeout={100}
            chartWrapperStyles={{
              maxWidth: '1200px'
            }}
            mainWrapperStyles={{
              backgroundColor: '#fff'
            }}
            startRunningTimeout={0}
          />
        </div>

        <div className="chart-row">
          <div className="description">
            <h3>Baseline</h3>
            <p>If your chart has negative values, you may want to set a <strong>baseline</strong>.</p>
            <p>The `baseline` property is any numeric value. In the example below, we set up a baseline of 0.</p>
            <SyntaxHighlighter language="javascript" style={hybrid}>
              {
`<DynamicBarChart
  data={this.state.data}
  baseline={0}
/>`
              }
            </SyntaxHighlighter>
          </div>
          <DynamicBarChart
            data={helpers.generateData(
              20,
              mocks.baselineWithNegatives,
              { prefix: "Year", initialValue: 1980 },
              100
            )}
            barHeight={30}
            showTitle={false}
            iterationTimeout={1000}
            startRunningTimeout={1500}
            baseline={0}
            mainWrapperStyles={{
              backgroundColor: '#fff'
            }}
          />
        </div>

        <div className="chart-row">
          <div className="description">
            <h3>Run by Command</h3>
            <p>By default, the animation starts automatically. You may choose to start the animation after a certain timeout:</p>
            <SyntaxHighlighter language="javascript" style={hybrid}>
              {
`<DynamicBarChart
  data={this.state.data}
  // Wait 2500 ms before animation starts
  startRunningTimeout={2500}
/>`
              }
            </SyntaxHighlighter>
            <p>Another option is to start the animation programmatically by adding a start button, and disabling the `startAutomatically` flag, and adding the `showStartButton` flag:</p>
            <SyntaxHighlighter language="javascript" style={hybrid}>
              {
`<DynamicBarChart
  data={this.state.data}
  startAutomatically={false}
  showStartButton={true}
  startButtonText={'Click Me!'}
/>`
              }
            </SyntaxHighlighter>
          </div>
          <DynamicBarChart
            data={helpers.generateData(100, mocks.runByCommand)}
            showTitle={false}
            iterationTimeout={100}
            barHeight={20}
            chartWrapperStyles={{
              maxWidth: '1200px'
            }}
            mainWrapperStyles={{
              backgroundColor: '#fff'
            }}
            startAutomatically={false}
            showStartButton={true}
            startButtonText={'Click Me!'}
            onRunStart={() => {
              console.log('It\'s on!');
            }}
          />
        </div>
        <div className="chart-row">
          <div className="description">
            <h3>Styles Go Crazy!</h3>
            <p>Although Dynamic Charts have default styling, you have various of option to control and change how your chart looks like.</p>
            <p></p>
            <SyntaxHighlighter language="javascript" style={hybrid}>
              {
`<DynamicBarChart
  data={this.state.data}
  barHeight={50}
  mainWrapperStyles={{
    backgroundColor: '#333',
    color: '#fff'
  }}
  chartWrapperStyles={{
    maxWidth: '800px',
    padding: '40px 20px'
  }}
  labelStyles={{
    fontSize: '35px'
  }}
  iterationTitleStyles={{
    color: '#fff',
    fontSize: '24px',
    backgroundColor: '#999',
    borderRadius: '50em',
    position: 'absolute',
    left: '50%',
    transform: 'translateX(-50%)',
    top: '440px',
    padding: '10px 30px'
  }}
/>`
              }
            </SyntaxHighlighter>
          </div>
          <DynamicBarChart
            data={helpers.generateData(100, mocks.stylesGoCrazy, { prefix: 'Year', initialValue: 2020 })}
            iterationTimeout={100}
            startRunningTimeout={4000}
            barHeight={50}
            chartWrapperStyles={{
              maxWidth: '1200px',
              padding: '40px 20px'
            }}
            mainWrapperStyles={{
              backgroundColor: '#333'
            }}
            iterationTitleStyles={{
              color: '#fff',
              fontSize: '24px',
              backgroundColor: '#999',
              borderRadius: '50em',
              position: 'absolute',
              left: '50%',
              transform: 'translateX(-50%)',
              top: '440px',
              padding: '10px 30px'
            }}
            labelStyles={{
              fontSize: '35px'
            }}
          />
        </div>
      </div>
    );
  }
}
